body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
  height: 100%;
  background: hsl(180, 12%, 92%);
}

button {
  background: 0;
  border: 0;
  cursor: pointer;
}

img {
  max-width: 100%;
}

.saylua {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.sidebar {
  width: 200px;
  position: -webkit-sticky;
  position: sticky;
}

.interaction-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  max-width: 1000px;
  margin: auto;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.change-sprite {
  border: 0;
  background: white;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  padding: 0;
  margin: 4px;
  cursor: pointer;
  transition: -webkit-transform 0.1s;
  transition: transform 0.1s;
  transition: transform 0.1s, -webkit-transform 0.1s;
  font-size: 20px;
  border: 2px solid transparent;
}

.change-sprite.selected {
  border: 2px solid hsl(165, 82%, 51%);
}

.change-sprite:hover {
  -webkit-transform: translateY(-0.5%);
          transform: translateY(-0.5%);
}

.change-sprite:active {
  -webkit-transform: translateY(0.5%);
          transform: translateY(0.5%);
}

.place-list {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
  padding: 0 8px;
}

.sprite-list {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 90%;
  margin: auto;
}

.button {
  margin: 0.25em 4px;
  padding: 0.25em 8px;
  border-radius: 16px;
  font-size: 16px;
  border: 1px solid hsla(0, 0%, 0%, 0.15);
  box-shadow: 0 1px 2px hsla(0, 0%, 0%, 0.15);
  background: white;
  cursor: pointer;
  transition: background .2s ease-in-out,
    border-color .2s ease-in-out,
    color .2s ease-in-out;
}

.button[disabled] {
  opacity: 0.5;
}

.button:hover {
  background: #e7f8f6;
  border-color: hsla(165, 82%, 51%, 50%);
}

.button:active {
  -webkit-transform: translateY(2%);
          transform: translateY(2%);
}
.sprite-headshot {
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  max-width: 100%;
  max-height: 100%;
}

.sprite-headshot img {
  position: absolute;
  max-width: none;
}

.interaction-view {
  -webkit-flex-grow: 0;
          flex-grow: 0;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

.interaction-content {
  text-align: left;
  padding: 0.25em 16px;
}

.event-text {
  padding: 0.5em 0;
  line-height: 1.4em;
  font-size: 16px;
}
.sprite-portrait {
  transition: -webkit-transform 0.1s;
  transition: transform 0.1s;
  transition: transform 0.1s, -webkit-transform 0.1s;
  max-width: 100%;
  max-height: 100%;
  background: none;
  border: 0;
  box-shadow: none;
}

.sprite-portrait[disabled] {
  cursor: default;
}

.sprite-portrait img {
  max-width: 100%;
  max-height: 100%;
}

.sprite-portrait:hover:enabled {
  -webkit-transform: translateY(-0.5%);
          transform: translateY(-0.5%);
  background: none;
  border: 0;
}

.sprite-portrait.sprite-is-excited:hover:enabled {
  -webkit-animation: bounce-up 2s linear infinite;
          animation: bounce-up 2s linear infinite;
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
}

.sprite-portrait:active:enabled {
  -webkit-transform: translateY(0.5%);
          transform: translateY(0.5%);
}

.sprite-portrait.flipped img {
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}

@-webkit-keyframes bounce-up {
  0%, 100% {
    -webkit-transform: translateY(0%) scaleY(1);
            transform: translateY(0%) scaleY(1);
  }
  50% {
    -webkit-transform: translateY(-3%) scaleY(1.01);
            transform: translateY(-3%) scaleY(1.01);
  }
}

@keyframes bounce-up {
  0%, 100% {
    -webkit-transform: translateY(0%) scaleY(1);
            transform: translateY(0%) scaleY(1);
  }
  50% {
    -webkit-transform: translateY(-3%) scaleY(1.01);
            transform: translateY(-3%) scaleY(1.01);
  }
}
.wilderness-background {
  background-size: cover;
  background-position: bottom;
  width: 660px;
  height: 510px;
  position: relative;
  overflow: hidden;
  left: 0;
  top: 0;
}

.wilderness-sprite-image {
  position: absolute;
  bottom: 0%;
  left: 0%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.wilderness-scene-item {
  position: absolute;
  background: none;
  border: 0;
  bottom: 0%;
  left: 0%;
  pointer-events: none;
}

.wilderness-scene-item img {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.character-view {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.pet-avatar-view {
  position: relative;
  width: 100%;
  padding-bottom: 2rem;
  padding-left: 2rem;
}

.pet-avatar-view .avatar-view {
  display: block;
  text-align: center;
}

.pet-avatar-view .avatar-view img {
  max-width: 20rem;
  max-height: 25rem;
}

.pet-avatar-view .active-pet-view {
  position: absolute;
  z-index: 50;
  bottom: 0;
  left: 0;
  width: 70%;
}
